import React from 'react';
import { bool, node, number, oneOf, oneOfType, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-dot';
const CSS_STATE = 'vds-state';

const Dot = ({ children, className, disabled, label, tabIndex, type, ...remainingProps }) => (
  <div
    className={classnames({
      [`${CSS_PREFIX}--${type}`]: Boolean(type),
      [`${CSS_STATE}--disabled`]: Boolean(disabled)
    }, className)}
    tabIndex={disabled ? -1 : tabIndex}
    {...remainingProps}
  >
    <div className={`${CSS_PREFIX}--screen-reader`}>{label}</div>
    <div className={`${CSS_PREFIX}-label`} aria-hidden="true" role="presentation">
      {children}
    </div>
  </div>
);

Dot.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Disabled
   */
  disabled: bool,

  /**
   * For screen reader
   */
  label: string,

  /**
   * @ignore
   */
  tabIndex: oneOfType([number, string]),

  /**
   * Type
   */
  type: oneOf(['message', 'comment'])
};

Dot.defaultProps = {
  type: 'message'
};

Dot.displayName = 'Dot';

export default Dot;
